import styled from "styled-components";

export const BoxRadientLeft = styled.div`
    background: linear-gradient(90deg, #DF1FCB 0%, rgba(217, 217, 217, 0) 102.22%);
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 3px;
    border-radius: 5px;
    img{
        background: ${props => props.imgBg ? props.imgBg : "#03031B"};
    }
`
export const BoxRadientRight = styled.div`
    background: linear-gradient(270deg, #DF1FCB 0%, rgba(217, 217, 217, 0) 102.22%);
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 3px;
    border-radius: 5px;
    img{
        background: #03031B;
    }
`

export const DevBox = styled.div`
    background: linear-gradient(135deg, rgba(0, 115, 255, 1), rgba(219, 0, 255, 1) 100%);
    display: flex;
    flex-direction: column;
    padding: 1px;
`
