import styled from "styled-components";

export default function CheckPoint () {
    return(
        <Wrapper>
            <Point/>
        </Wrapper>
    )
}

const Wrapper = styled.div`
    min-width: 30px;
    min-height: 30px;
    border: 1px solid #00E66D;
    border-radius: 50%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
`
const Point = styled.div`
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background: #00E66D;
`