import { BoxRadientLeft } from "components/box"
import { Container, Row, Column } from "components/layout"
import styled from "styled-components";
import JettImage from 'components/image';
import { SharedImage } from "constants/image-constants";
import CheckPoint from "components/checkpoint";

const { HEXADO_HADES_ROADMAP, HEXADO_HADES_DROP, HEXADO_ROADMAP, HEXADO_VISHNU_DROP, HEXADO_VISHNU_ROADMAP, HEXADO_DEVIL_DROP, HEXADO_DEVIL_ROADMAP, HEXADO_CLEPTO_ROADMAP, HEXADO_CLEPTO_DROP, HEXADO_HEXAVERSE_PRIVATE_ROADMAP, HEXADO_HEXAVERSE_PRIVATE_DROP, HEXADO_HEXAVERSE_PUBLIC, HEXADO_HEXAVERSE_PUBLIC_DROP } = SharedImage;

export default function RoadmapPage() {
    return (
        <Wrapper>
            <Container>
                <Row justifyContent="center">
                    <JettImage src={HEXADO_ROADMAP} className="hexado-roadmap" />
                </Row>
                <Column alignItems="center" gap="50px" className="hexado-roadmap-list">
                    <RoadmapWrapper>
                        <Column alignItems="center" className="hexado-roadmap-util">
                            <CheckPoint />
                            <Splitter />
                        </Column>
                        <BoxRadientLeft imgBg="#101126" className="hexado-roadmap-wrapper">
                            <Row gap="30px" alignItems="flex-start" className="hexado-roadmap-content">
                                <JettImage src={HEXADO_HADES_ROADMAP} />
                                <Column className="hexado-roadmap-des">
                                    <JettImage src={HEXADO_HADES_DROP} />
                                    <p>
                                        Hades drop is our establishing drop, the first out of four. After the mint, holders will be able to immediately trade their NFTs. This shall be interesting. We’ll see how our various economic attributes and utilities affect our base price, economy, trade volume, and so on!
                                        It doesn't end here, this is just the first out of four!
                                    </p>
                                </Column>
                            </Row>
                        </BoxRadientLeft>
                    </RoadmapWrapper>
                    <RoadmapWrapper>
                        <Column alignItems="center" className="hexado-roadmap-util">
                            <CheckPoint />
                            <Splitter />
                        </Column>
                        <BoxRadientLeft imgBg="#101126" className="hexado-roadmap-wrapper">
                            <Row gap="30px" alignItems="flex-start" className="hexado-roadmap-content">
                                <JettImage src={HEXADO_VISHNU_ROADMAP} />
                                <Column className="hexado-roadmap-des">
                                    <JettImage src={HEXADO_VISHNU_DROP} />
                                    <p>
                                        The Vishnu drop is our second drop. Halfway through! After the mint, holders will be able to immediately buy/sell/trade their NFTs. It will be neat to see how many people use our new “trading” system.
                                        Don't worry we're not done yet, two more to go!
                                    </p>
                                </Column>
                            </Row>
                        </BoxRadientLeft>
                    </RoadmapWrapper>
                    <RoadmapWrapper>
                        <Column alignItems="center" className="hexado-roadmap-util">
                            <CheckPoint />
                            <Splitter />
                        </Column>
                        <BoxRadientLeft imgBg="#101126" className="hexado-roadmap-wrapper">
                            <Row gap="30px" alignItems="flex-start" className="hexado-roadmap-content">
                                <JettImage src={HEXADO_DEVIL_ROADMAP} />
                                <Column className="hexado-roadmap-des">
                                    <JettImage src={HEXADO_DEVIL_DROP} />
                                    <p>
                                        Devil drop is the third drop. After the mint, holders will be able to immediately buy/sell/trade their NFTs. One can wonder how many people will take advantage of our utility system!  The “Devil” NFT is definitely one of the most unique drops due to how asset-heavy it is.

                                    </p>
                                </Column>
                            </Row>
                        </BoxRadientLeft>
                    </RoadmapWrapper>
                    <RoadmapWrapper>
                        <Column alignItems="center" className="hexado-roadmap-util">
                            <CheckPoint />
                            <Splitter />
                        </Column>
                        <BoxRadientLeft imgBg="#101126" className="hexado-roadmap-wrapper">
                            <Row gap="30px" alignItems="flex-start" className="hexado-roadmap-content">
                                <JettImage src={HEXADO_CLEPTO_ROADMAP} />
                                <Column className="hexado-roadmap-des">
                                    <JettImage src={HEXADO_CLEPTO_DROP} />
                                    <p>
                                        Clepto is this fourth and final drop! The Clepto utilities are pretty cool too. Let the trading and economy fall into place! After Cleptos completion the videogame will officially release playable by anyone who holds 1 or more NFT. The various utilities will also be deposited into holders account/game-profile as well. Exciting!

                                    </p>
                                </Column>
                            </Row>
                        </BoxRadientLeft>
                    </RoadmapWrapper>
                    <RoadmapWrapper>
                        <Column alignItems="center" className="hexado-roadmap-util">
                            <CheckPoint />
                            <Splitter className="hexado-roadmap-splitter" />
                        </Column>
                        <BoxRadientLeft imgBg="#101126" className="hexado-roadmap-wrapper">
                            <Row gap="30px" alignItems="flex-start" className="hexado-roadmap-content">
                                <JettImage src={HEXADO_HEXAVERSE_PRIVATE_ROADMAP} />
                                <Column className="hexado-roadmap-des">
                                    <JettImage src={HEXADO_HEXAVERSE_PRIVATE_DROP} />
                                    <p>
                                        The first Hexaverse release is playable by anyone who holds 1 or more Hexado’s Project NFTS. Essentially this is our first mainstream launch of the videogame. During the 3-4 week time period in which the game is private, 20% of all profits will be available in reward format for anyone who plays the game! The higher level you reach, the more you make.
                                    </p>
                                </Column>
                            </Row>
                        </BoxRadientLeft>
                    </RoadmapWrapper>
                    <RoadmapWrapper>
                        <Column alignItems="center" className="hexado-roadmap-util">
                            <CheckPoint />
                            <Splitter className="hexado-roadmap-splitter" />
                        </Column>
                        <BoxRadientLeft imgBg="#101126" className="hexado-roadmap-wrapper">
                            <Row gap="30px" alignItems="flex-start" className="hexado-roadmap-content">
                                <JettImage src={HEXADO_HEXAVERSE_PUBLIC} />
                                <Column className="hexado-roadmap-des">
                                    <JettImage src={HEXADO_HEXAVERSE_PUBLIC_DROP} />
                                    <p>
                                        The second Hexaverse release is now playable by anyone and everyone! Of course, anyone who isn't holding an NFT won't have access to any perks/money rewards, but will still be able to experience our fast-paced, engaging game!
                                    </p>
                                </Column>
                            </Row>
                        </BoxRadientLeft>
                    </RoadmapWrapper>
                </Column>
            </Container>
        </Wrapper>
    )
}

const Wrapper = styled.div`
    background-color: #03031B;
    color: white;
    font-family: Open Sans;
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding: 120px 20px;
    .hexado-roadmap-list{
        padding: 120px 0px;
    }
    .hexado-roadmap {
        max-width: 630px;
    }
    .hexado-roadmap-wrapper {
        max-width: 1250px;
        .hexado-roadmap-content{
            background-color: #101126;
            padding: 20px;
            .hexado-roadmap-des{
                flex: 1;
                min-width: 350px;
                @media screen and (max-width:620px){
                    min-width: 0px;
                    align-items: center;
                    p{
                        text-align: center;
                    }
                }
                p{
                    font-size: 20px;
                    line-height: 32px;
                }
            }
            @media screen and (max-width:620px) {
                flex-direction: column;
                align-items: center;
            }
        }
    }
    .hexado-roadmap-util {
        max-width: fit-content;
        height: calc(100% + 50px);
        @media screen and (max-width:680px) {
            display: none;
        }
    }
`

const Splitter = styled.div`
    background-color: #00E66D;
    width: 2px;
    flex: 1;
`

const RoadmapWrapper = styled.div`
    display: grid;
    grid-template-columns: min-content auto;
    gap: 50px;
    @media screen and (max-width:680px) {
        grid-template-columns: auto;
    }
    &:last-child{
        .hexado-roadmap-splitter{
            display: none;
        }
    }
`
